// tiny-slaider в секции history
const sliderOne = tns({
    container: '.history__grey-slider1',
    items: 1,
    slideBy: 'page',
    autoplay: false,
    controls: false,
    nav: false
  });

const sliderTwo = tns({
    container: '.history__grey-slider2',
    items: 1,
    slideBy: 'page',
    autoplay: false,
    controls: false,
    nav: false
});

document.querySelector('.history__grey-prev').addEventListener('click', () => {
    sliderOne.goTo('prev')
});
document.querySelector('.history__grey-next').addEventListener('click', () => {
    sliderOne.goTo('next')
});

document.querySelector('.history__grey-prev_right').addEventListener('click', () => {
    sliderTwo.goTo('prev')
});
document.querySelector('.history__grey-next_right').addEventListener('click', () => {
    sliderTwo.goTo('next')
});


//slider в секции choice
